@keyframes first-animation {
  0% {
    transform: scale(1, 1);
  }
  50% {
    transform: scale(0.8, 0.8);
  }
  100% {
    transform: scale(1, 1);
  }
}
@keyframes second-animation {
  0% {
    transform: scale(1, 1);
  }
  70% {
    transform: scale(0.7, 0.7);
  }
  80% {
    transform: scale(0.9, 0.9);
  }
  100% {
    transform: scale(1, 1);
  }
}

